body {
  color: #333;
  text-align: center;
  background-color: #f8f5ee;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

header {
  color: #fff;
  text-align: center;
  background: linear-gradient(to right, #ff914d, #ffcf81);
  padding: 20px 10px;
  font-size: 24px;
  font-weight: bold;
}

main {
  background: #fff;
  border-radius: 10px;
  width: 90%;
  max-width: 600px;
  margin: 20px auto;
  padding: 20px;
  box-shadow: 0 4px 10px #0000001a;
}

input, textarea, select {
  background-color: #fff8e1;
  border: 1px solid #ddd;
  border-radius: 5px;
  width: 100%;
  margin-top: 10px;
  padding: 12px;
  font-size: 16px;
}

button {
  color: #fff;
  cursor: pointer;
  background: #ff914d;
  border: none;
  border-radius: 5px;
  padding: 12px 15px;
  font-size: 16px;
  transition: background .3s, transform .2s;
}

button:hover {
  background: #ff721d;
  transform: scale(1.05);
}

ul {
  padding: 0;
  list-style: none;
}

li {
  background: #fff;
  border-radius: 5px;
  justify-content: space-between;
  align-items: center;
  margin: 10px 0;
  padding: 10px;
  display: flex;
  box-shadow: 2px 2px 5px #0000001a;
}

#ai-chatbot {
  color: #333;
  text-align: center;
  background-color: #fff8e1;
  border-radius: 10px;
  width: 90%;
  max-width: 500px;
  margin: 20px auto;
  padding: 15px;
  box-shadow: 0 4px 10px #0000001a;
}

#chatbox {
  flex-direction: column;
  gap: 10px;
  display: flex;
}

#chat-messages {
  text-align: left;
  background-color: #fffbf1;
  border-radius: 5px;
  flex-direction: column;
  gap: 8px;
  height: 300px;
  padding: 10px;
  font-size: 16px;
  display: flex;
  overflow-y: auto;
}

.user-message {
  color: #fff;
  text-align: left;
  white-space: pre-wrap;
  background-color: #ff914d;
  border-radius: 15px;
  align-self: flex-end;
  max-width: 80%;
  padding: 10px;
  font-weight: bold;
}

.ai-message {
  color: #000;
  text-align: left;
  white-space: pre-wrap;
  background-color: #ffcf81;
  border-radius: 15px;
  align-self: flex-start;
  max-width: 80%;
  padding: 10px;
}

#chat-input {
  color: #333;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 8px;
  width: calc(100% - 20px);
  padding: 12px;
  font-size: 16px;
}

#chat-send {
  color: #fff;
  cursor: pointer;
  background-color: #ff914d;
  border: none;
  border-radius: 8px;
  padding: 12px 20px;
  font-size: 16px;
  transition: background .3s;
}

#chat-send:hover {
  background-color: #ff721d;
}

footer {
  text-align: center;
  color: #333;
  background: #ffcf81;
  margin-top: 20px;
  padding: 15px;
  font-size: 14px;
}
